import { useEffect, useRef } from 'react';

/**
 * 
 * @param fn 
 */
export function useMountEffect(fn: () => (() => void) | void) {
  const mountedRef = useRef(false);

  useEffect(() => {
    if (mountedRef.current) {
      return;
    }

    mountedRef.current = true;
    return fn();
  }, [fn]);
}
